        <b-row>
          <b-col>
            <img
              style=" margin-top:15px; width:90%"
              src="public/images/image1.jpg"
              class="img-shadow lax"
              data-lax-translate-y="0 -500, 10000 6000"
              data-lax-anchor="self"
            />
          </b-col>
          <b-col>
            <img
