      <div class="container px-5 mx-auto">
        <div class="flex flex-col text-center w-full">
          <h1
            class="sm:text-3xl text-2xl font-medium title-font mb-4 text-gray-900"
          >Nuestros productos</h1>
          <p
            class="mx-auto leading-relaxed text-base"
          >Contamos con una amplia gama de productos, como travertino, nix, prfidos, lajas, orientados a las paredes y suelos; baldosas, mosaicos, molduras. Pueden ser utilizados en elementos como bancos, tocadores, tableros, chimeneas, azulejos, los bordes de la piscina, revestimiento, duchas, bachas.</p>
        </div>
