<script>
import AnimationLogo from "../animations/Logo.vue";
import { mapGetters } from "vuex";

export default {
  name: "app",
  data() {
    return {};
  },
  methods: {},

  computed: {
    ...mapGetters(["productos"])
  },
  components: {
    AnimationLogo: AnimationLogo
  },

  mounted: function() {
    console.log(this.productos);

      lax.setup(); // init

      const updateLax = () => {
        lax.update(window.scrollY);
        window.requestAnimationFrame(updateLax);
      };

      window.requestAnimationFrame(updateLax);
 
  }
};
</script>

<style scope>
.img-shadow {
  -webkit-box-shadow: 2px -4px 48px -21px rgba(0, 0, 0, 0.55);
  -moz-box-shadow: 2px -4px 48px -21px rgba(0, 0, 0, 0.55);
  box-shadow: 2px -4px 48px -21px rgba(0, 0, 0, 0.55);
}
.quienes-somos {
  margin-top: 100px;
  position: absolute;
  z-index: 10;
  background-color: #e7ded4ee;
  width: 100%;
}
.nuestros-productos{
        background:#f2efeb;
        margin-top:0px

}
@media screen and (max-width: 600px) {
  .quienes-somos {
    margin-top: -10px;
  }
  .nuestros-productos{
    margin-top:200px
  }
}

.background-hero {
  background: rgb(231, 222, 212);
  background: linear-gradient(
    180deg,
    rgba(231, 222, 212, 1) 0%,
    rgb(224, 224, 224) 50%,
    rgb(255, 255, 255) 100%
  );
}
.image-product {
  height: 100%;
  -webkit-box-shadow: 0px 3px 35px -19px rgba(0, 0, 0, 0.46);
  -moz-box-shadow: 0px 3px 35px -19px rgba(0, 0, 0, 0.46);
  box-shadow: 0px 3px 35px -19px rgba(0, 0, 0, 0.46);
}
</style>


