<template>
  <section>
    <section class="text-gray-700 body-font overflow-hidden">
              <div style="width:100% ">
           <h1><img
              style="width:100%; overflow:hidden"
              :alt="producto.id"
              :src="producto.imagen_banner"
            /></h1>
        </div>
      <b-container class="bv-example-row">
        <!-- Stack the columns on mobile by making one full-width and the other half-width -->


        <b-row class="container py-5" style="background:white !important">

          <b-col cols="12" md="12">
            <h1 class="text-xxl title-font tracking-widest">{{producto.nombre}}</h1>

            <p
              v-for="(contenido,index) in producto.desc"
              v-bind:key="index"
              class="leading-relaxed"
              data-lax-preset="fadeIn"
              style="z-index:1"
            >{{contenido}}</p>
          </b-col>
        </b-row>
      </b-container>
    </section>
  </section>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "app",
  data() {
    return {
      producto: {}
    };
  },
  props: ["id"],
  methods: {},
  computed: {
    ...mapGetters(["productos"])
  },
  components: {},
  watch: {
    $route(to, from) {
      window.scrollTo(0, 0);
      for (var i = 0; i < this.productos.length; i++) {
        if (this.productos[i].id == this.id) {
          this.producto = this.productos[i];
        }
      }
      if (!this.producto) {
        console.log = "no se encontr el producto";
      }
    }
  },

  mounted: function() {
    window.scrollTo(0, 0);
    for (var i = 0; i < this.productos.length; i++) {
      if (this.productos[i].id == this.id) {
        this.producto = this.productos[i];
      }
    }
    if (!this.producto) {
      console.log = "no se encontr el producto";
    }
  }
};
</script>

<style scope>
.background-quienes {
  background: rgb(231, 222, 212);
  background: linear-gradient(
    180deg,
    rgba(231, 222, 212, 1) 0%,
    rgb(224, 224, 224) 50%,
    rgb(255, 255, 255) 100%
  );
}
.bg-brown-500 {
  background: rgb(204, 197, 192);
}
</style>


