<template>
  <section>
    <section class="text-gray-700 body-font relative">
      <div class="absolute inset-0 bg-gray-300">
        <iframe
          style="filter: grayscale(1) contrast(1.2) opacity(0.6);"
          width="100%"
          height="100%"
          frameborder="0"
          marginheight="0"
          marginwidth="0"
          title="map"
          scrolling="no"
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d26287.406677068964!2d-58.46829566013525!3d-34.55543315576284!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x95bcb5d28526611d%3A0xbcb585a9ddc70069!2sBelgrano%2C%20Buenos%20Aires!5e0!3m2!1sen!2sar!4v1597699814232!5m2!1sen!2sar"
        ></iframe>
      </div>
      <div class="container px-5 py-24 mx-auto flex">
        <div
          class="lg:w-1/3 md:w-1/2 bg-white rounded-lg p-8 flex flex-col md:ml-auto w-full mt-10 md:mt-0 relative z-10"
        >
          <h2 class="text-gray-900 text-lg mb-1 font-medium title-font">Envianos un mensaje</h2>
          <input
            class="bg-white rounded border border-gray-400 focus:outline-none focus:border-indigo-500 text-base px-4 py-2 mb-4"
            placeholder="Email"
            type="email"
          />
          <textarea
            class="bg-white rounded border border-gray-400 focus:outline-none h-32 focus:border-indigo-500 text-base px-4 py-2 mb-4 resize-none"
            placeholder="Mensaje"
          ></textarea>

          <button
                      style="background-color:#916c4d"

            class="text-white bg-indigo-500 border-0 py-2 px-6 focus:outline-none hover:bg-indigo-600 rounded text-lg"
          >Enviar</button>
        </div>
      </div>
    </section>
  </section>
</template>

<script>
import AnimationLogo from "../animations/Logotipo.vue";
import MisionIcon from "../animations/Mision.vue";
import VisionIcon from "../animations/Vision.vue";
import ValorIcon from "../animations/Valor.vue";

export default {
  name: "app",
  data() {
    return {};
  },
  methods: {},
  computed: {},
  components: {
    AnimationLogo: AnimationLogo,
    MisionIcon: MisionIcon,
    VisionIcon: VisionIcon,
    ValorIcon: ValorIcon
  },

  mounted: function() {
    window.scrollTo(0, 0);
  }
};
</script>

<style scope>
.background-quienes {
  background: rgb(231, 222, 212);
  background: linear-gradient(
    180deg,
    rgba(231, 222, 212, 1) 0%,
    rgb(224, 224, 224) 50%,
    rgb(255, 255, 255) 100%
  );
}
.bg-brown-500 {
  background: rgb(204, 197, 192);
}
</style>


