<template>
  <div>
    <b-navbar toggleable="lg" type="dark" fixed="top" class="background-header">
      <b-navbar-brand href="#">
        <router-link to="/" tag="b-navbar-brand">

                <animation-logo
          style="width:70px"
          class="lg:w-2/6 md:w-3/6 w-5/6 mb-10 object-cover object-center rounded"
        ></animation-logo>

        </router-link>
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav>
          <b-navbar-nav>
            <router-link to="/nosotros/quienes-somos" tag="b-nav-item">La empresa</router-link>
          </b-navbar-nav>

          <b-nav-item-dropdown text="Productos " right>
            <router-link  v-for="producto in productos" v-bind:key="producto.id"
            :to="'/productos/'+producto.id" tag="b-dropdown-item">{{producto.nombre}}</router-link>
          </b-nav-item-dropdown>

          <b-navbar-nav>
            <router-link to="/contactanos" tag="b-nav-item">Contactanos</router-link>
          </b-navbar-nav>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </div>
</template>

<script>
import AnimationLogo from "../components/animations/Logotipo.vue";
import { mapGetters } from "vuex";

export default {
  name: "app",
  data() {
    return {
      nombre: "",
      mail: "",
      mensaje: "",
      notificaciones: [],
      notificacionesVistas: []
    };
  },
  props: {
    usuario: ""
  },
  components: {
    AnimationLogo: AnimationLogo
  },
  computed: {
    ...mapGetters(["productos"])
  },
  methods: {}
};
</script>


<style scoped>

.nav-item{
    font-family: "Dosis", sans-serif;
}
.dropdown-item{
    font-family: "Dosis", sans-serif;

}
.background-header {
  background: rgb(209, 209, 209);
  background: linear-gradient(90deg, rgb(59, 59, 59) 50%, rgb(65, 65, 65) 100%);

  -webkit-box-shadow: 0px 3px 35px -19px rgba(0, 0, 0, 0.46);
  -moz-box-shadow: 0px 3px 35px -19px rgba(0, 0, 0, 0.46);
  box-shadow: 0px 3px 35px -19px rgba(0, 0, 0, 0.46);
}
</style>


