import Home from "./components/Home.vue";
import Quienes from "./components/conocenos/Quienes.vue"
import Contacto from "./components/conocenos/Contacto.vue"

import Producto from "./components/Producto.vue"

export const routes = [
  { path: "/", component: Home },
  { path: "/nosotros/quienes-somos", component: Quienes},
  { path: "/contactanos", component: Contacto},

  { path: "/productos/:id", component:Producto, props: true }

];


